.SuccessCopy {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  z-index: 100;
  color: #fff;
  text-shadow: 1px 1px 1px #333;
  opacity: 0;
  pointer-events: none;
  -webkit-transition: opacity;
  -o-transition: opacity;
  transition: opacity;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.SuccessCopy_animate {
  -webkit-animation: bg 1.5s ease !important;
          animation: bg 1.5s ease !important;
  pointer-events: auto;
}

.SuccessCopy__wrap {
  width: 100%;
  text-align: center;
}
.SuccessCopy__wrap_animate {
  -webkit-animation: text 1.5s ease !important;
          animation: text 1.5s ease !important;
}

.SuccessCopy__text {
  width: 100%;
  padding: 20px 0 30px 0;
  background: #ffffff25;
  font-size: 80px;
  margin-bottom: 20px;
}
.SuccessCopy__title {
  font-size: 30px;
}

@-webkit-keyframes bg {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes bg {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes text {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
  }

  40% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 1;
  }
  60% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
  }
}

@keyframes text {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
  }

  40% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 1;
  }
  60% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
  }
}



